:root {
  --Blue: hsl(246, 80%, 60%);
  --Light-red-work: hsl(15, 100%, 70%);
  --Soft-blue-play: hsl(195, 74%, 62%);
  --Light-red-study: hsl(348, 100%, 68%);
  --Lime-green-exercise: hsl(145, 58%, 55%);
  --Violet-social: hsl(264, 64%, 52%);
  --Soft-orange-self-care: hsl(43, 84%, 65%);
  --Very-dark-blue: hsl(226, 43%, 10%);
  --Dark-blue: hsl(235, 46%, 20%);
  --Desaturated-blue: hsl(235, 45%, 61%);
  --Pale-Blue: hsl(236, 100%, 87%);
}
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Rubik";
}
h1 {
  font-size: 2.5rem;
  font-weight: 100;
}
h2 {
  font-size: 1.5rem;
}
h3 {
  font-size: 1.15rem;
}
h4,
p {
  font-size: 1rem;
}
body {
  background-color: var(--Very-dark-blue);
}
.main {
  max-width: 1080px;
  margin: 0 auto;
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1rem;
}
.btn {
  background: transparent;
  border: none;
  cursor: pointer;
}
.container {
  background-color: var(--Dark-blue);
  border-radius: 1rem;
  overflow: hidden;
  grid-column: 1 / 1;
  grid-row: 1/3;
  height: 500px;
  color: white;
}
.profile {
  background-color: var(--Blue);
  padding-left: 2rem;
  padding-top: 2rem;
  height: 60%;
  border-radius: 1rem;
}
.img {
  width: 40%;
  border: 2px solid white;
  border-radius: 50%;
  margin-bottom: 2rem;
}
.timeline {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  gap: 1rem;
}
.timeline > button {
  background: none;
  border: none;
  font-size: large;
  text-align: left;
  cursor: pointer;
  text-transform: capitalize;
  margin-left: 1.5rem;
  color: var(--Desaturated-blue);
}

.Work {
  background-color: var(--Light-red-work);
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
  z-index: 0;
}
.Play {
  background-color: var(--Soft-blue-play);
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
  z-index: 0;
}
.Study {
  background-color: var(--Light-red-study);
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
  z-index: 0;
}
.Exercise {
  background-color: var(--Lime-green-exercise);
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
  z-index: 0;
}
.Social {
  background-color: var(--Violet-social);
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
  z-index: 0;
}
.SelfCare {
  background-color: var(--Soft-orange-self-care);
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
  z-index: 0;
}
.svg {
  width: 70px;
  position: absolute;
  z-index: -1;
  right: 1rem;
}
.innerContainer {
  border-radius: 1rem;
  margin-top: 20%;
  background-color: var(--Dark-blue);
  height: 100%;
  z-index: 1;
}
.innerContainer:hover {
  cursor: pointer;
  background-color: var(--Desaturated-blue);
}
.heading {
  display: flex;
  justify-content: center;
  align-content: space-between;
  gap: 9.5rem;
  width: 100%;
  padding-top: 1rem;
  color: white;
}
.time {
  padding-left: 1.5rem;
  padding-top: 2rem;
  color: rgb(143, 143, 143);
  height: 100%;
}
.time > h1 {
  padding: 1rem 0;
  color: white;
}
@media screen and (max-width: 700px) {
  .main {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-top: 2rem;
  }
  .img {
    width: 70px;
    height: 70px;
    background-size: cover;
  }
  .profile {
    display: flex;
    gap: 2rem;
  }
  .container {
    height: auto;
  }
  .timeline {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 4rem;
    margin: 1rem 0;
  }
  .heading {
    display: flex;
    justify-content: center;
    align-content: space-between;
    gap: 20rem;
    width: 100%;
    padding-top: 1rem;
    color: white;
  }
  .time {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10rem;
    padding-left: 1.5rem;
    padding: 1rem;
    color: rgb(143, 143, 143);
    height: 100%;
  }
  .innerContainer {
    margin-top: 10%;
  }
}
